import React from 'react'
import card1 from '../../components/gesture/pictures/card1.jpg'
import module from "../../components/gesture/pictures/module.jpg"
import easy1 from "../../components/gesture/pictures/easy1.jpg"
import {
    Typography as Text,
    AppBar,
    CssBaseline,
    Toolbar,
    Grid,
    Container,
    Box,
    makeStyles
  } from "@material-ui/core";
const useStyles=makeStyles(()=>({
  root:{
    width:'1000px',
    margin:'0 auto',
    '& .header':{
      marginTop:'50px'
    },
    '& .sub_header':{
      width:'600px',
      margin:'0 auto',
      paddingBottom:'30px'
    }
  },
  container:{
    height:'200px',
    marginTop:'50px',
    '& img':{
        height:'200px',
        float:'right',
        width:'300px'
    },
    '& .title':{
        marginRight:'400px'
    }
  },
  containerl:{
    height:'200px',
    marginTop:'80px',
    '& img':{
        height:'200px',
        float:'left',
        width:'350px'
    },
    '& .title':{
        marginLeft:'500px'
    }
  },
  easy:{
    marginTop:'80px',
    marginBottom:'50px',
    width:'1000px',
    '& .content':{
      display:'flex',
      justifyContent:'space-around',
    },
    '& .son':{
      backgroundColor:'red',
      display:'inline-block',
      height:'100%',
      width:'25%',

    }
  }

}))
export default function Card() {
  const classes=useStyles();
  return (
    <div className={classes.root} >
      <div className="header">
        <Text variant="h3" color="textPrimary" align="center" gutterBottom>
          
        </Text>
      </div>
      <div className="sub_header">
        <Text variant="h6" align="center" color="textSecondary" paragraph>
        


        </Text>
      </div>
      <div className={classes.container}>
            <img src={card1}></img>
            <div className='title'>
            <Text variant="h5" color="textPrimary" align="left" gutterBottom>
              
           </Text>
           <Text variant="body1" color="textPrimary" align="left" gutterBottom>
           SocketC/S
           </Text>
           </div>
        </div>
        <div className={classes.containerl}>
        <img src={module}></img>
            <div className='title'>
            <Text variant="h5" color="textPrimary" align="right" gutterBottom>
              
           </Text>
           <Text variant="body1" color="textPrimary" align="right" gutterBottom>
            ANN + Softmax  + ReLU  + Dropout
+  + ReLU +  + Softmax 
           </Text>
           </div>
        </div>
      <div className={classes.easy}>
      <div className="header">
        <Text variant="h3" color="textPrimary" align="center" gutterBottom>
          
        </Text>
      </div>
      <div className="sub_header">
        <Text variant="h6" align="center" color="textSecondary" paragraph>
        

        </Text>
      </div>
      <div className='content'>
      <img className='son' src={easy1}></img>
      <img className='son' src={easy1}></img>
      <img className='son' src={easy1}></img>
      </div>
      </div>
     
      
     
    
</div>


  )
}
